.container {
  .tick {
    // font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    //   Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  }

  .upGreenArrow {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 4.5px 12px 4.5px;
    border-color: transparent transparent #00ff00 transparent;
  }

  .downRedArrow {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 12px 4.5px 0 4.5px;
    border-color: #ff0000 transparent transparent transparent;
  }

  .upArrow {
    position: relative;
    top: -15px;
    left: -10px;
    width: 0;
    height: 0;
    border: 6px solid transparent;
    border-bottom-color: #00ff00;
  }
  .upArrow:after {
    position: absolute;
    content: "";
    top: 20px;
    left: -3px;
    width: 6px;
    height: 10px;
    background: #00ff00;
  }

  .downArrow {
    position: relative;
    top: 18px;
    left: -10px;
    width: 0;
    height: 0;
    border: 6px solid transparent;
    border-top-color: #ff0000;
  }
  .downArrow:after {
    position: absolute;
    content: "";
    top: -15px;
    left: -3px;
    width: 6px;
    height: 10px;
    background: #ff0000;
  }

  /* d3 candle stick start */

  .axis path,
  .axis line {
    fill: none;
    stroke: black;
    shape-rendering: crispEdges;
  }

  .axis text {
    font-family: "Open Sans", sans-serif;
    font-size: 11px;
    fill: black;
  }
}
